// file MatchWordMeaning.tsx
import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Form } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import {
  TextareaContainer,
  Textarea,
  FormLabel,
  TimerContainer,
  Timer,
  ButtonContainer,
  StyledButton,
  GridContainer,
  Square
} from './MatchWordMeaning.styled';

// nh ngha kiu cho Square
interface SquareType {
  id: number;
  text: string;
  type: 'word' | 'meaning';
  disabled?: boolean; // Thm thuc tnh disabled vo y
}

const WordMatchingGame: React.FC = () => {
  const [inputText, setInputText] = useState<string>('');
  const [pairs, setPairs] = useState<{ word: string; meaning: string }[]>([]);
  const [squares, setSquares] = useState<SquareType[]>([]);
  const [selected, setSelected] = useState<number[]>([]);
  const [highlighted, setHighlighted] = useState<number[]>([]); // Thm trng thi  nh du cc  c chn
  const [time, setTime] = useState({ minutes: 0, seconds: 0, milliseconds: 0 });
  const [intervalId, setIntervalId] = useState<NodeJS.Timeout | null>(null);
  const [textareaHidden, setTextareaHidden] = useState<boolean>(false); // Trng thi  kim sot vic n/hien ni dung
  const [gameStarted, setGameStarted] = useState<boolean>(false); // Trng thi  kim sot tr chi  bt u

  useEffect(() => {
    // Cleanup timer on component unmount
    return () => {
      if (intervalId) {
        clearInterval(intervalId);
      }
    };
  }, [intervalId]);

  const startGame = () => {
    const lines = inputText.split('\n').filter(line => line.trim());
    const newPairs = lines.map(line => {
      const [word, meaning] = line.split(':').map(part => part.trim());
      return { word, meaning };
    });

    // Nu c nhiu hn 15 cp, chn ngu nhin 15 cp
    const selectedPairs = newPairs.length > 15 
      ? newPairs.sort(() => Math.random() - 0.5).slice(0, 15)
      : newPairs;

    const newSquares: SquareType[] = selectedPairs.flatMap((pair, index) => [
      { id: index * 2, text: pair.word, type: 'word' },
      { id: index * 2 + 1, text: pair.meaning, type: 'meaning' }
    ]);

    // Shuffle squares
    newSquares.sort(() => Math.random() - 0.5);

    setPairs(selectedPairs);
    setSquares(newSquares);
    setSelected([]);
    setHighlighted([]); // Reset highlighted squares
    setTime({ minutes: 0, seconds: 0, milliseconds: 0 });
    setTextareaHidden(true); // n ni dung textarea khi bt u tr chi
    setGameStarted(true); // Tr chi  bt u

    if (intervalId) {
      clearInterval(intervalId);
    }

    const id = setInterval(updateTime, 10); // Cp nht mi 10ms
    setIntervalId(id);
  };

  const stopGame = () => {
    if (intervalId) {
      clearInterval(intervalId);
    }
    setGameStarted(false); // Tr chi  dng
    setTextareaHidden(false); // Hin li textarea
  };

  const updateTime = () => {
    setTime(prev => {
      let { minutes, seconds, milliseconds } = prev;

      milliseconds += 10; // Tng 10ms
      if (milliseconds >= 1000) {
        milliseconds = 0;
        seconds += 1;
      }
      if (seconds >= 60) {
        seconds = 0;
        minutes += 1;
      }

      return { minutes, seconds, milliseconds };
    });
  };

  const calculateScale = (text: string, width: number, height: number): number => {
    const ratio = Math.min(width / text.length, height / 24); // iu chnh t l theo kch thc  v  di vn bn
    return ratio;
  };

  const handleSquareClick = (id: number) => {
    if (selected.length === 2 || squares.find(square => square.id === id)?.disabled) {
      return;
    }
  
    setSelected(prev => {
      const newSelected = [...prev, id];
  
      if (newSelected.length === 2) {
        const [firstId, secondId] = newSelected;
        const firstSquare = squares.find(square => square.id === firstId);
        const secondSquare = squares.find(square => square.id === secondId);
  
        if (firstSquare && secondSquare) {
          if (pairs.some(pair => 
            (pair.word === firstSquare.text && pair.meaning === secondSquare.text) ||
            (pair.word === secondSquare.text && pair.meaning === firstSquare.text)
          )) {
            // Nu ngi dng chn ng, disable cc  v thm vo highlighted
            setSquares(prevSquares => prevSquares.map(square => 
              square.id === firstId || square.id === secondId 
                ? { ...square, disabled: true } 
                : square
            ));
            setHighlighted(prev => [...prev, firstId, secondId]); // nh du cc   chn ng
          } else {
            // Nu ngi dng chn sai, reset la chn sau 1 giy
            setTimeout(() => setSelected([]), 250);
          }
        }
  
        setTimeout(() => setSelected([]), 250); // t li selected sau 1 giy
      }
  
      return newSelected;
    });
  };

  useEffect(() => {
    if (squares.every(square => square.disabled)) {
      stopGame(); // Dng tr chi khi tt c cc   b disable
    }
  }, [squares]);

  return (
    <div style={{ width: '70vw', padding: '0 15px', boxSizing: 'border-box' }}>
      <Row>
        <Col>
          <Form.Group controlId="exampleForm.ControlTextarea1">
            <FormLabel hidden={textareaHidden}>Danh sch t v ngha:</FormLabel>
            <TextareaContainer>
              <Textarea
                value={inputText}
                onChange={(e) => setInputText(e.target.value)}
                placeholder="Nhp danh sch t v ngha, mi cp trn mt dng, cch nhau bng du hai chm"
                hidden={textareaHidden}
              />
              <ButtonContainer>
                {gameStarted ? (
                  <StyledButton onClick={stopGame} style={{ backgroundColor: 'red' }}>Dng Li</StyledButton>
                ) : (
                  <StyledButton onClick={startGame}>Bt u</StyledButton>
                )}
                <TimerContainer>
                  <Timer>
                    {`${String(time.minutes).padStart(2, '0')}:${String(time.seconds).padStart(2, '0')}:${String(time.milliseconds / 10).padStart(2, '0')}`}
                  </Timer>
                </TimerContainer>
              </ButtonContainer>
            </TextareaContainer>
          </Form.Group>
          {gameStarted && (
            <GridContainer>
              {squares.map((square) => (
                <Square
                  key={square.id}
                  selected={selected.includes(square.id)}
                  disabled={square.disabled}
                  highlight={highlighted.includes(square.id)} // Kim tra  c c nh du khng
                  scale={calculateScale(square.text, 140, 135)} // Cp nht kch thc ca  vung
                  onClick={() => handleSquareClick(square.id)}
                  data-content={square.text}
                  type={square.type}
                />
              ))}
            </GridContainer>
          )}
        </Col>
      </Row>
    </div>
  );
};

export default WordMatchingGame;
